import api from '../../utils/api.js';
import util from '../../utils/util.js';
export default {
	data() {
		return {
			tab_index: 0,
			student_qrcode: '',
			teacher_qrcode: '',
			painter_data: '',
			painter_img: ''
		};
	},
	onLoad() {
		const that = this;
		// 学生二维码
		api.keeperQrcode({
			data: {
				type: 1
			},
			success: function(res) {
				that.student_qrcode = res;
			}
		});
		// 老师二维码
		api.keeperQrcode({
			data: {
				type: 2
			},
			success: function(res) {
				that.teacher_qrcode = res;
			}
		});
	},
	methods: {
		clickTab(e) {
			let index = e.currentTarget.dataset.index;
			this.tab_index = index;
			this.painter_img = '';
		},
		onImgOK(e) {
			uni.hideLoading();
			let imagePath = this.painter_img || e.detail.path;
			this.painter_img = imagePath;
			wx.getSetting({
				success(res) {
					if (res.authSetting['scope.writePhotosAlbum']) {
						wx.saveImageToPhotosAlbum({
							filePath: imagePath,
							success: function(){
								util.success("已保存到相册");
							}
						});
					} else if (res.authSetting['scope.writePhotosAlbum'] === false) {
						util.confirm('您还未授权访问相册,请移步设置授权。', function() {
							wx.openSetting();
						});
					} else {
						wx.authorize({
							scope: 'scope.writePhotosAlbum',
							success: function() {
								wx.saveImageToPhotosAlbum({
									filePath: imagePath,
									success: function(){
										util.success("已保存到相册");
									}
								});
							}
						});
					}
				}
			});
		},
		save() {
			const that = this;
			if(this.painter_img){
				this.onImgOK();
				return false
			}
			uni.showLoading({
				title: '正在生成海报'
			});
			let title = "";
			let qrcode = "";
			if(this.tab_index == 0){
				title = "邀请老师设计版面";
				qrcode = this.student_qrcode;
			}else if(this.tab_index == 1){
				title = "邀请同学设计版面";
				qrcode = this.teacher_qrcode;
			}
			let painter_data = {
				width: '750px',
				height: '878px',
				views: [
					{
						type: 'image',
						url: '../../static/bg_share.jpg',
						css: {
							top: '0px',
							left: '0px',
							width: '750px',
							height: '878px'
						}
					},
					{
						type: 'text',
						text: title,
						css: {
							width: '552px',
							fontSize: '26px',
							textAlign: 'center',
							color: '#fff',
							left: '99px',
							top: '147px'
						}
					},
					{
						type: 'image',
						url: qrcode,
						css: {
							width: '380px',
							height: '380px',
							borderWidth: '1px',
							borderColor: '#ffc9ce',
							borderRadius: '30px',
							top: '266px',
							left: '184px'
						}
					}
				]
			};
			this.painter_data = painter_data;
		}
	}
};
